@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  height: 100vh;
  width: 100%;
  background-color: #000000;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

@media (min-width: 20em) and (max-width: 46.24em) {
  #root {
    background-color: green;
    height: 100%;
  }
}

:root {
  --pixel-size: 6;
}

body {
  background: #96ffc1;
}

.Character {
  right : 30px;
  width: calc(32px * 6);
  width: calc(32px * var(--pixel-size));
  height: calc(32px * 6);
  height: calc(32px * var(--pixel-size));
  overflow: hidden;
  position: -webkit-sticky;
  position: sticky;
  margin: 4em auto;
}

.Character_spritesheet {
  -webkit-animation: moveSpritesheet 1s steps(4) infinite;
          animation: moveSpritesheet 1s steps(4) infinite;
  width: calc(128px * 6);
  width: calc(128px * var(--pixel-size));
  position: absolute;
  
}
.Character_shadow {
  position: absolute;
  width: calc(32px * 6);
  width: calc(32px * var(--pixel-size));
  height: calc(32px * 6);
  height: calc(32px * var(--pixel-size));
}

.pixelart {
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}

.face-right {
  top: calc(-32px * 6);
  top: calc(-32px * var(--pixel-size));
}
.face-up {
  top: calc(-64px * 6);
  top: calc(-64px * var(--pixel-size));
}
.face-left {
  top: calc(-96px * 6);
  top: calc(-96px * var(--pixel-size));
}


@-webkit-keyframes moveSpritesheet {
  from {
     -webkit-transform: translate3d(0px,0,0);
             transform: translate3d(0px,0,0)
  }
  to {
     -webkit-transform: translate3d(-100%,0,0);
             transform: translate3d(-100%,0,0)
  }
}


@keyframes moveSpritesheet {
  from {
     -webkit-transform: translate3d(0px,0,0);
             transform: translate3d(0px,0,0)
  }
  to {
     -webkit-transform: translate3d(-100%,0,0);
             transform: translate3d(-100%,0,0)
  }
}

.chatbot {
    font-family: Helvetica;
    border-radius: 10px;
    box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.15);
    background: #000;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    width: 400px;
    -webkit-flex-flow: column;
            flex-flow: column;
    overflow: hidden;
  }
  
  .header {
    padding: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    max-height: 58px;
    font-size: 16px;
    font-weight: bold;
    background-color: #262626;
  }
  
  .messages {
    width: 100%;
    height: 400px;
    overflow: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 10px 0px;
    padding-right: 17px; /* Increase/decrease this value for cross-browser compatibility */
    box-sizing: content-box;
  }
  
  .message-container-bot {
    width: 100%;
    text-align: left;
    margin-bottom: 10px;
    display: -webkit-inline-flex;
    display: inline-flex;
  }

  .message-container-client {
    width: 100%;
    text-align: left;
    margin-bottom: 10px;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
  }
  
  .bot-message {
    -webkit-animation-name: bounceIn;
            animation-name: bounceIn;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 1px 20px 20px 20px;
    min-width: 40px;
    background: #262626;
    color: #fff;
    max-width: 300pxpx !important;
    word-wrap: break-word !important;
    white-space: pre-line;
  }
  
  .user-message {
    -webkit-align-self: flex-end;
            align-self: flex-end;
    -webkit-animation-name: bounceIn;
            animation-name: bounceIn;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    padding: 10px 10px;
    margin: 10px;
    border-radius: 20px 1px 20px 20px;
    background: #cccccc;
    background: linear-gradient(
        180deg,
        rgba(139, 47, 184, 1) 0%,
        rgba(103, 88, 205, 1) 51%,
        rgba(89, 116, 219, 1) 92%
      )
      no-repeat center;
    background-attachment: fixed;
    color: #fff;
    max-width: 300px !important;
    word-wrap: break-word !important;
  }
  
  .input {
    position: relative;
  }
  
  .input > form > input[type="text"] {
    font-family: Helvetica;
    font-size: 16px;
    border: 0;
    border-radius: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top: 1px solid #eee;
    box-shadow: none;
    box-sizing: border-box;
    opacity: 1;
    outline: none;
    padding: 16px 52px 16px 10px;
    width: 100%;
    -webkit-appearance: none;
  }
  
  .input > form > input:focus {
    outline: none;
  }
  
  .input > form > button {
    background-color: transparent;
    border: 0;
    border-bottom-right-radius: 10px;
    box-shadow: none;
    cursor: pointer;
    fill: #4a4a4a;
    opacity: 1;
    outline: none;
    padding: 14px 16px 12px 16px;
    position: absolute;
    right: 0;
    top: 0;
  }
  
  @-webkit-keyframes bounceIn{
    0%{
      opacity: 0;
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
    }
    50%{
      opacity: 0.9;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
    80%{
      opacity: 1;
      -webkit-transform: scale(0.89);
              transform: scale(0.89);
    }
    100%{
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @keyframes bounceIn{
    0%{
      opacity: 0;
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
    }
    50%{
      opacity: 0.9;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
    80%{
      opacity: 1;
      -webkit-transform: scale(0.89);
              transform: scale(0.89);
    }
    100%{
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
